import React from "react";
import { Link } from "react-router-dom";

export default class Options extends React.Component {
  render() {
    return (
      <ul className="list list--justify">
        <li className="list__item">
          <button className="button button--bordered" onClick={this.props.restartLevel}>
            <span className="fa fa-refresh"></span>
          </button>
        </li>
        <li className="list__item">
          <Link className="button button--bordered" to="/">
            <span className="fa fa-home"></span>
          </Link>
        </li>
        <li className="list__item">
          {this.props.isDone ? 
            <Link className="button button--bordered" to={`/game/${this.props.level + 1}`}>
              <span className="fa fa-level-up"></span>
            </Link> : 
            <button className="button button--bordered" onClick={this.props.undoMove}>
              <span className="fa fa-history"></span>{" "}({this.props.memory.length})
            </button>
          }
        </li>
      </ul>
    );
  }
}
