import React from "react";
import { Link } from "react-router-dom";

export default class page404 extends React.Component {
  render() {
    return (
      <div className="page">
        <div className="page__header">
          <h1>Page 404</h1>
          <p className="page__description">Sorry. The page does not exist</p>
        </div>
        <Link className="button button--bordered" to="/">Back to Home</Link>
      </div>
    );
  }
}
