import React from "react";

// constants
import { UP, DOWN, LEFT, RIGHT } from "../constants";
import { WALL, BOX, PLAYER, GOAL, BOXONGOAL } from "../constants";

// components
import Display from "../components/display";
import Controls from "../components/controls";
import Options from "../components/options";
import GameOver from "../components/gameOver";

// data
import levels from "../data/levels.json";

export default class Game extends React.Component {
  state = {
    player: {
      row: 0,
      column: 0,
      direction: DOWN
    },
    map: {
      walls: [],
      boxes: [],
      goals: [],
      row: 0,
      column: 0
    },
    isDone: false,
    memory: [],
    displayOptions: false
  };

  componentDidMount() {
    this.restartLevel();
    document.addEventListener("keydown", this.handleKeyPress, false);    
  }

  componentDidUpdate(prevProps) {
    if (this.props.match.params.id !== prevProps.match.params.id) {
      this.restartLevel();
      this.setState({
        isDone: false
      })
    }
   }

  componentWillUnmount(){
    document.removeEventListener("keydown", this.handleKeyPress, false);
  }

  restartLevel = () => {
    let player = {};
    let walls = [];
    let boxes = [];
    let goals = [];
    let mapCounter = {row: 0, column: 0};
    const level = Number(this.props.match.params.id) - 1;
    const map = levels.classic[level];
    const mapSplit = map.split("/");

    mapSplit.map((mapRow, rowCounter) => {
      mapRow.split("").map((mapCell, colCounter) => {
        switch (mapCell) {
          case PLAYER:
            player.row = rowCounter;
            player.column = colCounter;
            player.direction = DOWN;
            break;
          case BOXONGOAL:
            boxes = [...boxes, { row: rowCounter, column: colCounter }];
            goals = [...goals, { row: rowCounter, column: colCounter }];
            break;            
          case BOX:
            boxes = [...boxes, { row: rowCounter, column: colCounter }];
            break;
          case GOAL:
            goals = [...goals, { row: rowCounter, column: colCounter }];
            break;
          case WALL: 
            walls = [...walls, { row: rowCounter, column: colCounter }];
            break;
          default:
            break;
        }

        mapCounter.column = colCounter > mapCounter.column ? colCounter : mapCounter.column;
        return null;
      });

      mapCounter.row = rowCounter > mapCounter.row ? rowCounter : mapCounter.row;      
      return null;
    });

    mapCounter.row++;
    mapCounter.column++;
    this.setState({
      player,
      map: {
        ...mapCounter,
        walls,
        boxes,
        goals,
      },
      level,
      memory: [],
      isDone: false
    });
  };

  handleButton = direction => () => {
    let newPlayerPosition = { row: this.state.player.row, column: this.state.player.column };
    let walls = [...this.state.map.walls];
    let boxes = [...this.state.map.boxes];
    let goals = [...this.state.map.goals];
    let tileInFront = { ...newPlayerPosition };
    let memory = [...this.state.memory];
    let boxesInGoal = 0;
    const wallsString = JSON.stringify(walls);
    const boxesString = JSON.stringify(boxes);
    const goalsString = JSON.stringify(goals);

    switch (direction) {
      case UP:
        newPlayerPosition.row--;
        tileInFront.row -= 2;
        break;
      case DOWN:
        newPlayerPosition.row++;
        tileInFront.row += 2;
        break;
      case LEFT:
        newPlayerPosition.column--;
        tileInFront.column -= 2;
        break;
      case RIGHT:
        newPlayerPosition.column++;
        tileInFront.column += 2;
        break;
      default:
        break;
    }

    if (
      boxesString.includes(JSON.stringify(newPlayerPosition)) &&
      !boxesString.includes(JSON.stringify(tileInFront)) &&
      !wallsString.includes(JSON.stringify(tileInFront))
    ) {
      boxes = JSON.parse(
        boxesString.replace(
          JSON.stringify(newPlayerPosition),
          JSON.stringify(tileInFront)
        )
      );

      if (memory.length === 10) memory.shift();

      this.setState({
        player: { ...newPlayerPosition, direction },
        memory: [
          ...memory,
          { player: this.state.player, boxes: this.state.map.boxes }
        ],
        map: {
          ...this.state.map,
          boxes
        }
      });
    } else if (
      !wallsString.includes(JSON.stringify(newPlayerPosition)) &&
      !boxesString.includes(JSON.stringify(newPlayerPosition))
    ) {
      this.setState({
        player: { ...newPlayerPosition, direction }
      });
    }
    else {
      this.setState({
        player: { ...this.state.player, direction }
      });
    }

    boxes.map(box => {
      const boxString = JSON.stringify(box);

      if (goalsString.match(boxString)) boxesInGoal++;
      if (boxesInGoal === goals.length) {
        this.props.addToCompleteLevels(Number(this.props.match.params.id) - 1)();
        this.setState({
          isDone: true
        });
      }

      return null;
    });
  };

  undoMove = () => {
    if (this.state.memory.length) {
      const memory = this.state.memory.pop();

      this.setState({
        player: memory.player,
        map: {
          ...this.state.map,
          boxes: memory.boxes
        }
      });
    }
  };

  toggleOptions = () => {
    this.setState({
      displayOptions: !this.state.displayOptions
    })
  }

  handleKeyPress = (event) => {
    if (this.state.isDone === false) {
      switch(event.key) {
        case 'ArrowUp':
        case 'w':
          this.handleButton(UP)();
          break;
        case 'ArrowDown':
        case 's':
          this.handleButton(DOWN)();
          break;
        case 'ArrowLeft':
        case 'a':
          this.handleButton(LEFT)();
          break;
        case 'ArrowRight':
        case 'd':
          this.handleButton(RIGHT)();
          break;
        default:
          break;
      }
    }
  }

  render() {
    const { isDone, memory, displayOptions } = this.state;
    const toggleClass = 'button button--bordered gamepage__toggle ';

    return (
      <div className="page gamepage">
        <Display data={this.state} />
        <div className="gamepage--mobileView">
          <button onClick={this.toggleOptions} className={!isDone ? toggleClass : toggleClass + 'gamepage__toggle--hidden'}>
            <span className="fa fa-bars"></span>
          </button>
          {displayOptions ? <Options restartLevel={this.restartLevel} undoMove={this.undoMove} memory={memory} isDone={isDone}/> : 
          !isDone ? <Controls handleButton={this.handleButton}/> : 
            <div>
              <GameOver />
              <Options restartLevel={this.restartLevel} undoMove={this.undoMove} memory={memory} isDone={isDone} level={Number(this.props.match.params.id)}/>
            </div>
        }</div> 
        <div className="gamepage--desktopView">
          {!isDone ? <Controls handleButton={this.handleButton}/> : <GameOver />}
          <Options restartLevel={this.restartLevel} undoMove={this.undoMove} memory={memory} isDone={isDone} level={Number(this.props.match.params.id)}/>
        </div>
      </div>
    );
  }
}
