import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";

// pages
import Home from "./pages/home";
import Game from "./pages/game";
import Page404 from "./pages/page404";

// data
import levels from './data/levels.json';

export default class App extends React.Component {
  state = {
    completeLevels: []
  }

  addToCompleteLevels = level => () => {
    this.setState({
      completeLevels: [...this.state.completeLevels, level]
    })
  }

  render() {
    return (
      <HashRouter>
        <Switch>
          <Route exact path="/" render={() => <Home completeLevels={this.state.completeLevels}/>} />
          <Route exact path="/game/:id" render={(props) => {
            const level = Number(props.match.params.id);
            const withinLevel = level > 0 && level <= levels.classic.length;
        
            if (isNaN(level) || !withinLevel) return <Page404 />

            return <Game {...props} addToCompleteLevels={this.addToCompleteLevels}/>
          }} />

          <Route component={Page404} />
        </Switch>
      </HashRouter>
    );
  }
}
