import React from "react";

// constants
import { UP, DOWN, LEFT, RIGHT } from "../constants";

export default class Controls extends React.Component {
  render() {
    return (
      <div className="controller">
        <div className="controller__top">
          <button className="button button--bordered" onClick={this.props.handleButton(UP)}>
            <span className="fa fa-angle-up"></span>
          </button>
        </div>
        <div className="controller__middle">
          <button className="button button--bordered" onClick={this.props.handleButton(LEFT)}>
            <span className="fa fa-angle-left"></span>
          </button>
          <button className="button button--bordered" onClick={this.props.handleButton(RIGHT)}>
            <span className="fa fa-angle-right"></span>
          </button>
        </div>
        <div className="controller__bottom">
          <button className="button button--bordered" onClick={this.props.handleButton(DOWN)}>
            <span className="fa fa-angle-down"></span>
          </button>
        </div>
      </div>
    );
  }
}
