import React from "react";

export default class Display extends React.Component {
  isPlayer = (player, cell) => player.row === cell.row && player.column === cell.column
  isBox = (boxesString, cell) => boxesString.match(JSON.stringify(cell))
  isGoal = (goalsString, cell) => goalsString.match(JSON.stringify(cell))
  isWall = (wallssString, cell) => wallssString.match(JSON.stringify(cell))

  render() {
    const { player, map } = this.props.data;
    const wallssString = JSON.stringify(map.walls);
    const boxesString = JSON.stringify(map.boxes);
    const goalsString = JSON.stringify(map.goals);
    let mapLayout = [];

    for (let row = 0; row < map.row; row++) {
      let mapRow;
      let mapRowArray = [];

      for (let column = 0; column < map.column; column++) {
        const cell = {row, column};
        let mapCell = <td key={`${row} ${column}`} className={
          this.isPlayer(player, cell) ? `tile tile--player${player.direction}` :
          this.isBox(boxesString, cell) && this.isGoal(goalsString, cell) ? 'tile tile--boxOnGoal' : 
          this.isBox(boxesString, cell) ? 'tile tile--box' : 
          this.isGoal(goalsString, cell) ? 'tile tile--goal' : 
          this.isWall(wallssString, cell) ? 'tile tile--wall' : 'tile'
        }/>;

        mapRowArray = [...mapRowArray, mapCell];
        mapRow = <tr key={row}>{mapRowArray}</tr>
      }

      mapLayout = [...mapLayout, mapRow]
    }

    return (
      <table className="gamepage__map">
        <tbody>{mapLayout}</tbody>
      </table>
    );
  }
}
